  .product {
    position: absolute;
    top: 40%;
    left: 46%;
    transition: transform 1000ms ease;
    transition-delay: 500ms;
  }

 .product:hover {
    transform: scale(2);
  }

  .product:hover > .products_list > .product_item > img{
    transform: scale(1);
    transition: transform 500ms;
    transition-delay: 1000ms;
  }

  img{
    transform:scale(0);
    transition: transform 500ms ease-in;
  }

  /* img:hover{
    transform:scaleX(1.2);
    transition: transform 500ms;
  } */

  img{
    height: 25px;
    width: 25px;
    border-radius: 50%;
    border: 1px solid black;
  }